
.VRHeaderBox {
    overflow: hidden;
    background-color: #ffffff;
    padding-top: 1vh;
    padding-bottom: 1vh;
}

.hamburger_nav {
    height: 2vh;
    padding-left: 30px;
}

.VRLogo {
    height: 5vh;
    padding-left: 20px;
}


.ResumePDF {
    width: 100%;
    height: 84vh;
    padding-left: 55px;
    padding-right: 55px;
    padding-top: 20px;
}
.VRFooterBox {
    overflow: hidden;
    background-color: #F2F2F7;
    height: 8.4vh;
    width: 100%;
}

.VRFooter {
    min-height: 100%;
    min-width: 100%;
}

.placeCard {
    height: 30px;
    width: 75%;
    background-color: #ffffff;
    border-radius: 2px;
    margin: 0 auto;
    margin-top: 1vw;
    box-shadow: 1px 1px 2px #888888;


}


.placeCard h1{
    font-size: 15px;
    vertical-align: middle;
    font-family: 'Noto Sans', sans-serif;
    /* 'Exo' sans-serif, Verdana, Arial, monospace; */
    float: left;
    margin-top: 8px;
    margin-left: 3%;
}

.dropdown {
    float: right;
    height: 40%;
    margin-right: 3%;
    margin-top: 2%;
}

.evidence_drawer {
    border-radius: 10px;
    width: 75%;
    margin: 0 auto;
    margin-top: .75vw;

}

.switch {
    height: 1.5vh;
    width: 3vh;
    vertical-align: middle;
}

.switch-toggle {
    height: 1.4vh;
    width: 1.4vh;
}

.switch.on .switch-toggle {
    left: 1.5vh;
}

.evidenceCard {
    width: 100%;
    background-color: #ffffff;
    box-shadow: 1px 1px 2px #888888;
    /* margin-top: 1%; */
    padding-top: 1%;
    padding-bottom: 1%;

}

.evidenceCardDetail {
    text-align: right;
}

.evidenceCard h1 {
    margin-left: 10%;
    vertical-align: middle;
    display: inline-block;


}

.evidenceCardDetail h1{
    margin-right: 5%;
}


.nopadding h1{
    font-size: 15px;
    font-family: 'Noto Sans', sans-serif;
    height: 15px;
    line-height: 25px;
}

.nopadding {
    padding: 0 !important;
    margin: 0 !important;
    background-color: #ffffff;
 }

 .evidence_image {
    max-height: 20px;
    max-width: 25%;

}

