.plus_sign_verifiable {
    float: left;
    height: 20px;
    margin-left: 10px;
    margin-top: 5px;
}


.plus_sign_verifiable_gone {
    float: left;
    height:  0px;
    width: 0px;
    margin-left: 2%;
    margin-top: 1%;
}
.plus_sign_text {
    max-width: 100%;
    max-height: 100%;
}

.modal_dialog_text{
    position: absolute;
    top: 32%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) !important;
            transform: translate(-50%, -50%) !important;
}

.modal_text_input {
    height: 70px;
    width: 25vw;
    font-size: 20px;
    border-bottom-left-radius: 5px; 
    border-top-left-radius: 5px;
    border: 0cm;
    float: left;

}

.submit_button {
    float:right;
    background-color: #19AA8B;
    height: 70px;
    border-bottom-left-radius: 0px;
    border-top-left-radius: 0px;
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px; 
    outline: none;

}   


.modal-transparent .modal-content {
    background-color: #9c1c1c00;
    border: 0cm;
}

.modal_text_input:focus {
    outline-width: 0;
}




.section_navigation {
    width : 100%;
    height: 25px;
    margin-top: 10vh;
}

.inner_navigation_holder {
    width: 70%;
    height: 100%;
    margin-left: 15%;
    display: inline-block;
}
.section_navigation h1 {
    text-align: center;
    vertical-align: middle;
    font-size: 25px;
    font-family: 'Exo', sans-serif; 

}
.section_arrow_left {
    float: left;
    height: 100%;

}

.section_arrow_right {
    float: right;
    height: 100%;

}

.constant_width {
    min-width: 100%;
}

.plus_sign_section {
    height: 35px;
    width: 35px;
    margin-right: 6%;
    float: right;
}

.plus_sign_experience {
    margin-left: 48%;
    height: 4%;
    width: 4%;

}

.container-fluid {
    padding: 0;
}
.row {
    margin-left: 0;
    margin-right: 0;
}
body {
    background-color: #F2F2F7;
}
